.banner {
  position: relative;
  padding: 24vh 1%;
}
.banner .slide_banner {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  max-width: 100% !important;
  height: 125vh;
}
.banner .slide_banner .banner_img, .banner .slide_banner .banner_one, .banner .slide_banner .banner_two, .banner .slide_banner .banner_three {
  width: 100%;
  height: 100%;
  background-position: center;
  background-size: cover;
  position: absolute;
  transition: 1s;
  left: 0;
  top: 0;
}
.banner .slide_banner .selected {
  opacity: 1;
}

@media only screen and (orientation: portrait) {
  .banner_img, .banner_one, .banner_two, .banner_three {
    background-size: contain !important;
    background-position: top !important;
    background-repeat: no-repeat !important;
  }
  .banner_one {
    background-image: url("../../public/images/ipad_one.jpg") !important;
  }
  .banner_two {
    background-image: url("../../public/images/ipad_two.jpg") !important;
  }
  .banner_three {
    background-image: url("../../public/images/ipad_three.jpg") !important;
  }
}
@media (max-width: 780px) {
  .banner {
    background-size: cover;
    padding: 24vh 1%;
  }
  .tablet_resolution {
    display: block !important;
  }
}
@media (max-width: 500px) {
  .banner {
    height: 100vh !important;
    background-image: url("../../public/images/mob_one.png");
  }
  .slide_banner {
    display: none;
  }
}